.EmotionItem{
    cursor: pointer;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.EmotionItem img{
    width: 50%;
    margin-bottom: 10px;
}
.EmotionItem span{
    font-size: 18px;
}
.EmotionItem_off{
    background: #ececec;
} 
.EmotionItem_on_1{
    background: #64c964;
    color: #fff;
} 
.EmotionItem_on_2{
    background: #9dd772;
    color: #fff;
} 
.EmotionItem_on_3{
    background: #fdce17;
    color: #fff;
} 
.EmotionItem_on_4{
    background: #fd8446;
    color: #fff;
} 
.EmotionItem_on_5{
    background:#fd565f;
    color: #fff;
} 