.DiaryList{}
.DiaryList .menu_wrapper{
    margin-top: 20px;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
}
.DiaryList .left_col  select{
    margin-right: 10px;
    border: none;
    border-radius: 5px;
    background-color: #ececec;
    padding:10px 20px;
    cursor: pointer;
    font-family: 'Nanum Pen Script', cursive;
    font-size: 18px;
}
.DiaryList .right_col{flex-grow: 1;}
.DiaryList .right_col button{width: 100%;}